.feature-banner {
  background-color: @blue;
  color: @white;
  font-weight: 500;
  height: 70px;
  margin-top: -15px;
  width: 100%;

  ul.feature-list {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  li {
    align-self: center;
    flex: 1;
    text-align: center;

    @media (min-width: 768px) {
      padding-top: 15px;
    }
  }

  span {
    vertical-align: middle;
  }

  svg {
    fill: @white;
    margin-right: 12px;
    max-height: 28px;
    max-width: 28px;
    vertical-align: middle;

    path {
      stroke-width: 3;
    }
  }
}

#delivery-svg {
  path {
    stroke: #fff;
    stroke-width: 3;
  }
}
