#account-svg {
  height: 30px;
  width: 30px;
}

.account-details {
  background-color: @white;
  display: none;
  min-height: 340px;
  max-width: 777px;
  opacity: 0;
  padding: 20px;
  position: absolute;
  top: 150px;
  right: 100px;
  transition: opacity 0.3s, transform 0.3s;
  z-index: 99;

  & > div {
    padding: 20px 40px;
  }

  .btn {
    position: absolute;
    bottom: 50px;
  }

  input,
  a {
    display: block;
    margin-top: 25px;
  }
}

.account-active {
  display: flex;
  opacity: 1;
  transition: opacity 0.3s, transform 0.3s;
}

.account-title {
  font-weight: 700;
  font-size: 1.25rem;
}

.header-account a.active {
  background-color: @blue;
  display: block;

  svg {
    fill: @white;
  }
}

.new-customer {
  border-left: 1px solid @lightgrey;
  max-width: 360px;
}
