.nav-toggle {
  cursor: pointer;
  display: inline-block;
  height: 45px;
  order: 1;
  position: relative;
  width: 45px;
  z-index: 99;

  @media (min-width: 768px) {
    display: none;
  }
}

.nav-toggle-bar {
  background-color: @black;
  display: block;
  height: 4px;
  margin: 10px 0;
  transition: background-color 0.25s;
}

.open {
  & span:first-child {
    transform: rotate(-45deg) translateY(10px);
    margin-left: -10px;
  }

  & span:last-child {
    transform: rotate(45deg) translateY(-10px);
    margin-left: -10px;
  }

  .nav-toggle-bar {
    background-color: @white;
  }
}

.categories-nav {
  display: none;

  position: absolute;
  top: 0;
  width: 100%;
  z-index: 20;

  @media (min-width: 768px) {
    display: block;
    position: relative;
  }
}

.nav-menu {
  background-color: @black;
  margin: 0;
  padding: 20px;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;

  @media (min-width: 768px) {
    flex-direction: row;
    justify-content: flex-start;
  }

  li {
    border-bottom: 0.05em solid @grey;
    display: inline-block;
    text-align: left;
    padding: 15px 0;

    @media (min-width: 768px) {
      border-bottom: none;
      padding: 0 20px;
    }
  }

  li:last-child {
    border-bottom: none;
  }

  li.nav-item:before {
    content: "";
    position: absolute;
    right: 20px;
    width: 0;
    height: 0;
    margin-top: -8px;
    border-style: solid;
    border-width: 8px 10px 8px 0;
    border-color: transparent @white-smoke transparent transparent;
    transition: transform 0.25s;
    margin-top: 8px;

    @media (min-width: 768px) {
      display: none;
    }
  }

  li.nav-item.open:before {
    transform: rotate(-90deg);

    @media (min-width: 768px) {
      display: none;
    }
  }
}

nav {
  margin: 0 auto;
  position: relative;
  text-align: center;
  width: 100%;
  z-index: 100;

  a {
    font-size: 0.9375rem;
    font-weight: 600;
  }
}

.account-menu {
  color: @white;
  @media (min-width: 768px) {
    display: none;
  }

  svg {
    fill: @white;
    max-width: 30px;
  }
}

//Submenus
.menu1 {
  padding: 100px 20px 0 20px;

  @media (min-width: 768px) {
    padding: 20px;
  }
}

.menu2 {
  display: none;
  font-weight: normal;
  margin-left: 26px;

  a {
    font-weight: 400;
  }

  @media (min-width: 768px) {
    display: none !important;
  }
}

.account-menu li:first-child {
  border-top: 2px solid @white;
}

.flex {
  display: flex;
}
