.primary-header {
  align-items: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.desktop-block {
  display: none;

  @media (min-width: 768px) {
    display: block;
  }
}

.desktop-ib {
  display: none;

  @media (min-width: 768px) {
    display: inline-block;
  }
}

.top-bar {
  background-color: @blue;
  height: 18px;
  width: 100%;

  @media (min-width: 768px) {
    height: 42px;
  }
}

.header-links {
  margin: 0;
  padding: 0;

  @media (min-width: 768px) {
    align-items: center;
    display: flex;

    justify-content: flex-end;
    padding: 0 20px;
  }

  li {
    display: inline-block;
    padding: 0 0.9375rem;
    vertical-align: top;

    &:not(:first-child) {
      border-left: 1px solid @white;
    }
  }

  a {
    color: @white;
    font-size: 0.9375rem;

    &:hover,
    &:focus {
      color: @yellow;
      transition: color 0.25s;
    }
  }
}

@media (min-width: 768px) {
  .header-links > li:last-child {
    background-color: @yellow;
    border-left: 0;
    height: 42px;
    margin: -7px 0 -7px 10px;
    max-width: 200px;
    padding: 0;
  }

  .header-links > li:last-child a {
    color: @black;
    display: block;
    font-weight: 600;
    line-height: 1.4;
    padding: 7px 50px 0 50px;
    position: relative;
    text-align: center;

    &:after {
      border-color: @yellow transparent transparent;
      border-style: solid;
      border-width: 30px 100px 0;
      content: "";
      height: 0;
      left: 0;
      position: absolute;
      top: 42px;
      z-index: -1;
    }
  }
}

.header-content {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 26px 20px;
}

.logo {
  order: 1;
  max-width: 50%;
  //margin: 0 auto;

  @media (min-width: 768px) {
    margin-right: 30px;
    width: 287px;
  }
}

.primary-header svg {
  fill: @black;
  height: 100%;
  transition: fill 0.25s;
  width: 100%;
}

.nav-action {
  background-color: transparent;
  display: block;
  height: 30px;
  padding: 0;
  transition: background-color 0.3s;
  width: 30px;

  @media (min-width: 768px) {
    padding: 8px;
  }
}

.header-basket {
  order: 3;

  svg {
    width: 26px;
  }
}

#search-form {
  order: 4;
}

@media (min-width: 768px) {
  #search-form {
    order: 2;
  }

  .header-phone {
    font-size: 1.25rem;
    margin: 0 40px;
    order: 4;
  }
  .header-account {
    order: 5;
  }
  .header-basket {
    order: 6;
  }
}
