@font-face {
  font-family: Raleway;
  src: url(../fonts/Raleway-Regular.ttf);
}

body {
  color: @black;
  font-family: "Raleway";
  line-height: 1.75;
  font-size: 16px;
  font-weight: 400;
}

img {
  max-width: 100%;
}

ul {
  height: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: @white;
  text-decoration: none;

  &:hover,
  &:focus {
    color: @blue;
  }
}

.hide {
  display: none;
}

.show {
  display: block;
}
