.home-hero {
  align-items: center;
  background-image: url("@{img-pathway}/commercial-washrooms-header-graphic.png");
  background-repeat: no-repeat;
  background-size: cover;
  color: @white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 20vh;
  min-height: 450px; //493px
  width: 100%;

  .hero-wrapper {
    line-height: 1.2;
    text-align: center;

    @media (min-width: 768px) {
      width: 60%;
    }
  }
}

.banner1 {
  font-size: 2.6rem;
  font-weight: 600;
  margin-bottom: 20px;
  text-shadow: 0 0 16px rgba(0, 0, 0, 0.35);
}

.banner2 {
  font-size: 1.8rem;
  text-shadow: 0 0 16px rgba(0, 0, 0, 0.44);
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 768px) {
  .banner1 {
    font-size: 4rem;
  }

  .banner2 {
    font-size: 2.3rem;
  }
}
