#search-form {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  width: 100%;

  @media (min-width: 768px) {
    flex-grow: 1;
    margin-top: 0;
    text-align: right;
    width: auto;
  }

  div {
    //display: inline-block;
  }

  input {
    font-size: 0.9375rem;
    font-weight: 600;
  }
}

.search-form {
  flex-grow: 1;

  label > span {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
}

input#search {
  width: 75%;
}

//Results
.search-results {
  background-color: @white;
  display: none;
  text-align: left;
  position: absolute;
  z-index: 99;
  top: 150px;
  left: 18%;
  //opacity: 0;
  padding: 15px;
  max-width: 520px;
  transition: opacity 0.25s;
}

.search-results img {
  width: 30%;
}

.product-summary {
  display: inline-block;
  width: 66%;
}

.product-title a {
  color: @black;
  font-weight: 600;
}

.product-description {
  color: @grey;
  font-size: 0.75rem;
}
